import React, { FC } from "react";
import styles from "../LoginForm/LoginForm.module.css";
import FormHeading from "../FormHeading/FormHeading";
import {
  Button,
  EmailInput,
  Input,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import FormNavigationElement from "../FormNavigationElement/FormNavigationElement";
import { useForm } from "../../hooks/useForm";
import { postRegistrationDetails } from "../../services/asyncActions/register";
import { useAppDispatch } from "../../utils/constants";

const RegistrationForm: FC = () => {
  const dispatch = useAppDispatch();
  const { values, handleChange, isValid } = useForm({
    name: "",
    email: "",
    password: "",
  });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    dispatch(postRegistrationDetails(values));
  };

  return (
    <form noValidate onSubmit={handleSubmit} className={styles.form}>
      <FormHeading text="" extraClass="mb-6" />
      <Input
        name={"name"}
        placeholder=""
        extraClass="mb-6"
        onChange={handleChange}
        value={values.name || ""}
        required={true}
      />
      <EmailInput
        name={"email"}
        placeholder="E-mail"
        extraClass="mb-6"
        onChange={handleChange}
        value={values.email || ""}
        required={true}
      />
      <PasswordInput
        name={"password"}
        placeholder=""
        extraClass="mb-6"
        onChange={handleChange}
        value={values.password || ""}
        required={true}
      />
      <Button disabled={!isValid} htmlType="submit" extraClass="mb-20">
        
      </Button>
      <FormNavigationElement
        text=" ?"
        linkText=""
        route="/login"
        extraClass="mb-6"
      />
    </form>
  );
};

export default RegistrationForm;
